    z-index: 3;
    transform: translateY(0%);
    background-color: #fff;
    transition: all 0.4s ease; }
    .Stage__reveal--active {
      transform: translateY(-100%); }
    .Stage__reveal__content {
      padding: 2.5rem 1.5rem;
      flex: 1; }
  .Stage__closeBtn {
    display: block;
    width: 100%;
